.TodoListItem-virtualized {
    &+& {
        border-top: 1px solid #dee2e6;
    }

    &:nth-child(even) {
        background: #f8f9fa;
    }
}

.TodoListItem {
    padding: 1rem;
    display: flex;
    align-items: center;

    .checkbox {
        cursor: pointer;
        flex: 1;
        display: flex;
        align-items: center;

        svg {
            font-size: 1.5rem;
        }

        .text {
            margin-left: 0.5rem;
            flex: 1;
        }

        &.checked {
            // svg {
            //     color: #9775fa;
            // }

            .text {
                color: #adb5bd;
                text-decoration: line-through;
            }
        }
    }

    .remove {
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        color: #ff6b6b;
        cursor: pointer;

        &:hover {
            color: #ff8787;
        }
    }
}