@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  padding: 0;
  background: #e9ecef;
  font-family: 'Noto Sans KR', sans-serif;
}

input {
  font-family: 'Noto Sans KR', sans-serif;
}
.TodoTemplate {
  width: 512px;
  margin: 6rem auto 0;
  border-radius: 4px;
  overflow: hidden; }
  .TodoTemplate .app-title {
    background: #7950f2;
    color: white;
    height: 4rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center; }
  .TodoTemplate .content {
    background: white; }

.TodoInsert {
  display: flex;
  background: #495057; }
  .TodoInsert input {
    background: none;
    outline: none;
    border: none;
    padding: 0.5rem;
    font-size: 1.125rem;
    line-height: 1.5;
    color: white;
    flex: 1 1; }
    .TodoInsert input::-webkit-input-placeholder {
      color: #dee2e6; }
    .TodoInsert input::-moz-placeholder {
      color: #dee2e6; }
    .TodoInsert input:-ms-input-placeholder {
      color: #dee2e6; }
    .TodoInsert input::-ms-input-placeholder {
      color: #dee2e6; }
    .TodoInsert input::placeholder {
      color: #dee2e6; }
  .TodoInsert button {
    background: none;
    outline: none;
    border: none;
    background: #868e96;
    color: white;
    padding: 0 1rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    -webkit-transition: 0.1s background ease-in;
    transition: 0.1s background ease-in; }
    .TodoInsert button:hover {
      background: #adb5bd; }

.TodoListItem-virtualized + .TodoListItem-virtualized {
  border-top: 1px solid #dee2e6; }

.TodoListItem-virtualized:nth-child(even) {
  background: #f8f9fa; }

.TodoListItem {
  padding: 1rem;
  display: flex;
  align-items: center; }
  .TodoListItem .checkbox {
    cursor: pointer;
    flex: 1 1;
    display: flex;
    align-items: center; }
    .TodoListItem .checkbox svg {
      font-size: 1.5rem; }
    .TodoListItem .checkbox .text {
      margin-left: 0.5rem;
      flex: 1 1; }
    .TodoListItem .checkbox.checked .text {
      color: #adb5bd;
      text-decoration: line-through; }
  .TodoListItem .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer; }
    .TodoListItem .remove:hover {
      color: #ff8787; }

.TodoList {
  min-height: 320px;
  max-height: 410px;
  overflow-y: auto; }

.TodoPalette {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  background: #495057; }

.color {
  width: 2rem;
  height: 2rem;
  box-sizing: border-box;
  cursor: pointer; }
  .color.checked, .color:hover {
    border: 2px solid white; }
  .color + .color {
    margin-left: 0.5rem; }

